.pie_Diagram {
  padding-top: 30px;
  padding-bottom: 10px;
  width: 400px;
  height: 400px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#ils_Value {
  height: 20px;
  text-align: center;
}

button {
  display: block;
  color: black;
  background-color: white;
  width: 130px;
  height: 80px;
  font-size: 25px;
  font-weight: bold;
  font-family: arial;
  letter-spacing: 0.2;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  box-shadow: 0px 0px 15px black;
  border-radius: 15%;
  border: 1px solid black;
}

button:hover {
  cursor: pointer;
  color: aqua;
}

button:active {
  border: 2px solid black;
  box-shadow: 0px 0px 5px black;
}